.Popout {
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background: rgba(0, 0, 0, .3);
    transition-duration: .2s;
    touch-action: none;
}

.Popout_container {
    z-index: 12;
    background: #2f4359;
    padding: 18px;
    border-radius: 24px;
    min-width: 215px;
    display: flex;
    flex-direction: column;
    touch-action: none;
    margin: 0 auto;
    opacity: 0;
    -webkit-animation: Popout .2s forwards;
    animation: Popout .2s forwards;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, .33);

    color: #fff;
}

.Popout_img {
    margin: -36px auto 0;
    border-radius: 50%;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, .3);
    background: #212b35;
}

.Popout_title {
    font-size: 20px;
    font-weight: 600;
    padding: 12px 0 4px
}

.Popout_desc, .Popout_title {
    text-align: center
}

.Popout_buttons {
    display: flex;
    margin: 15px auto -36px;
}

.Popout_buttons button {
    background-color: #41f6df;
}

.Popout_buttons .no {
    margin-left: 10px;
}

.Popout_button {
    width: 100%;
    text-align: center;
    font-weight: 600
}

@-webkit-keyframes Popout {
    0% {
        opacity: 0;
        transform: translateY(20px)
    }
    to {
        opacity: 1
    }
}

@keyframes Popout {
    0% {
        opacity: 0;
        transform: translateY(20px)
    }
    to {
        opacity: 1
    }
}