.container {
    position: absolute;
    width: 100%;
    height: 100%;

    color: #fff;
}

.centered-content {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.result-content {
    top: 30%;
}

.first-screen {
    width: 345px;

    text-align: center;
}

.first-screen .title {
    font-size: 22px;
    font-weight: bold;
}

.first-screen .desc {
    font-size: 16px;
}

.first-screen .watermark {
    color: #41f6df;
}

.margin-top-1 {
    margin-top: 1rem;
}

.margin-top-2 {
    margin-top: 2rem;
}

.question-screen {
    width: 345px;

    position: relative;
}

.question-screen .number {
    font-weight: 500;
    font-size: 20px;

    padding-left: 5px;
}

.question-screen .number .current {
    color: #41f6df;
}

.question-screen .box {
    border-radius: 10px;
    padding: 1rem;
    height: 160px;
    position: relative;

    -webkit-box-shadow: 0 15px 29px 4px rgba(0, 0, 0, 0.31);
    -moz-box-shadow: 0 15px 29px 4px rgba(0, 0, 0, 0.31);
    box-shadow: 0 15px 29px 4px rgba(0, 0, 0, 0.31);
}

.question-screen .box:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;

    background: rgb(22, 19, 17);
    background: linear-gradient(0deg, rgba(22, 19, 17, 1) 0%, rgba(21, 22, 29, 0) 55%);
}

.question-screen .answers button {
    margin-top: 1rem;
    width: 100%;
    padding: 3px;
}

.question-screen .controls {
    display: flex;
}

.question-screen .controls .answer {
    margin-left: 1rem;

    background-color: #41f6df;
}

.question-screen .controls .help {
    border: 1px solid #41f6df;
    color: #41f6df;
}

.question-screen .box .text {
    position: absolute;
    width: 100%;

    bottom: 30px;
    left: 0;

    font-weight: 500;
    font-size: 20px;
    text-align: center;

    z-index: 1;
}

.question-screen .box .watermark {
    position: absolute;
    width: 100%;

    bottom: 10px;
    left: 0;

    font-size: 13px;
    text-align: center;

    z-index: 1;
}

.result-screen {
    width: 300px;

    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.image-result {
    width: 360px;
    position: absolute;
    left: 50%;
    bottom: 0;

    transform: translate(-50%, 0);
}

.result-screen .image {
    border-radius: 10px;
    padding: 1rem;
    height: 130px;
    position: relative;

    -webkit-box-shadow: 0 15px 29px 4px rgba(0, 0, 0, 0.31);
    -moz-box-shadow: 0 15px 29px 4px rgba(0, 0, 0, 0.31);
    box-shadow: 0 15px 29px 4px rgba(0, 0, 0, 0.31);
}

.result-screen .right-answers {
    color: #41f6df;
}

.result-screen .name {
    color: #41f6df;

    font-size: 30px;
    font-weight: bold;

    text-transform: uppercase;
}

.result-screen .actions button {
    margin-top: 1rem;
    width: 100%;
}

.confetti {
    margin-left: 30px;
}

.group-photo {
    border-radius: 50%;
}

.popout-icon-padding {
    padding: 15px;
}
